import React, { Component } from 'react';
import { importAll } from "utils";

class GameCard extends Component {
    render() {
        const webpackContext = require.context('assets/img/search-result', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        return (
            <div className="game-card">
                <div className="game-card__l">
                    <div className="game-card__l_img" style={{backgroundImage: `url(${images['game_intro_u1773.svg']})`}}>
                    </div>
                </div>
                <div className="game-card__r">
                    <div className="game-card__r-t">
                        [TRAINER CD] Ro-Ghoul [ALPHA]
                    </div>
                    <div className="game-card__r-m">
                        <div className="game-card__r-m__overview">
                            <span>适合2-64人游戏</span>
                            <span>2019-5-12最后更新</span>
                            <span>212个赞</span>
                            <span>254人正在游戏</span>
                        </div>
                        <div className="game-card__r-m__brief">
                            Hello guys! We are glad to present you brand new design of our marketplace.
                            We have completely redesigned craftwork.design according to the latest trends. Now it looks more attractive and intuitive. Also, all other author's product...
                        </div>
                    </div>
                    <div className="game-card__r-b">
                        <img className="game-card__r-b_img" src={images['user_avatar_u44.svg']} />
                        <span className="game-card__r-b_by">By</span>
                        <span className="game-card__r-b_name">meta.elements</span>
                    </div>
                </div>
            </div>
        )
    }
}

export default GameCard;