import React, {Component} from 'react';
import { withRouter } from 'react-router-dom';
import * as qs from 'query-string';

import Header from "components/partial/header/Header";
import UserCard from "components/partial/card/UserCard";
import GameCard from "components/partial/card/GameCard";
import Search from "components/partial/search/Search";

class SearchResult extends Component {

    constructor(props) {
        super(props);
    }

    state = {
       queryString: ''
    }

    componentDidMount() {

    }

    render() {
        const { history } = this.props;
        let QS = qs.parse(history.location.search);
        console.log(history);
        this.queryString = QS['query'];
        // this.setState({
        //     queryString: QS['query']
        // })
        console.log(123 , '  - - - - -' )
        // history.search
        return (
            <>
                <Header/>
                <div className="search-main">
                    <Search phone={true}/>

                    <div className="search-result">
                        搜索结果<span>“{this.queryString&& `${this.queryString}`}”</span>
                    </div>

                    <div className="category">
                        <span className="category-title">玩家(2)</span>
                    </div>

                    <div className="user-card__list">
                        <UserCard online={true}/>
                        <UserCard />
                    </div>

                    <div className="category">
                        <span className="category-title">游戏(2)</span>
                    </div>

                    <div className="game-card__list">
                        {
                            new Array(2).fill(1).map(() => (
                                <GameCard />
                            ))
                        }
                    </div>
                </div>
            </>
        );
    }
}

export default withRouter(SearchResult);